import React from 'react';
import "../styles/Loading.css";
import Parrot from "../images/trainspot.gif";




 
class Loading extends React.Component {
  render() {
    return <div className="loading">
           <img src={Parrot} alt="parrot"/>
          </div>
  }
}




export default Loading;


